.wrapper{
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: grey;
}

/* ----- Menu ------ */

.menu {
  z-index: 5;
  display: flex;
  position: relative;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 15px;
  -webkit-text-stroke: 0.25px lightgray;
}

.menuButton {
  z-index: 5;
  font-family: Arial, monospace;
  top: 3px;
  right: 114px;
  height: 30px;
  width: 30px;
  display: flex;
  align-items: center;
  background: none;
  position: absolute;
  background: white;
  border-color: black;
  border-radius: 50%;
  filter: opacity(0.3) drop-shadow(2px 4px 6px black);
  transition: all 0.2s;
}

.popupMenu {
  z-index: 5;
  position: fixed;
}

.boxMenu {
  z-index: 5;
  position: fixed;
  top: 63px;
  right: 0%;
  width: 225px;
  margin: 0;
  height: 125px;
  min-height: 10vh;
  background-color: rgba(255,255,255,0.5);
  border-radius: 10px;
  padding: 20px;
  border: 1px solid black;
  font-family: Arial, monospace;
  filter: drop-shadow(2px 4px 6px black);
}

.closeMenuIcon {
  z-index: 5;
  position: fixed;
  right: calc(2%);
  top: calc(4%);
  background: #ededed;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  line-height: 20px;
  text-align: center;
  border: 1.5px solid black;
  font-size: 20px;
  filter: opacity(0.3);
  transition: all 0.2s;
}

.citySelector {
  width: 65%;
}

.menuWrapper {
  position: fixed;
  color: black;
  z-index: 5;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: top;
  align-items: center;
  font-family: Arial, monospace;
  filter: text(2px 4px 6px black);
}

/* ----- Video Player ----- */

.videoResponsive {
  position: relative;
  z-index: 1;
  width: 300%;
  height: 100vh;
  left: -100%;
  padding-bottom: 56.25%;
  padding-top: 25px;
}

.videoResponsive iframe {
  position: absolute;
  z-index: 1;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
}

.videoWrapper {
  overflow: hidden;
  max-width: 100%;
}

.muteButton {
  z-index: 5;
  top: 3px;
  right: 40px;
  border-radius: 50%;
  height: 30px;
  width: 30px;
  display: flex;
  align-items: center;
  filter: opacity(0.3);
  position: fixed;
  background: white;
  border-color: black;
  transition: all 0.2s;
}

.muteIcon {
  transform: scale(1.3);
}

/* ----- Music Player ----- */

.music {
  z-index: 5;
  height: 80px;
  width: 250px;
  position: absolute;
  top: 4px;
  left: 3px;
  border: none;
  transition: all 0.2s;
}

.music iframe {
  height: 80px;
  width: 250px;
  filter: opacity(0.1);
  transition: all 0.2s;
}

.music iframe:hover {
  height: 450px;
  width: 450px;
  filter: opacity(1) drop-shadow(2px 4px 6px black);
}

.shuffleButton {
  z-index: 5;
  top: 3px;
  right: 77px;
  border-radius: 50%;
  height: 30px;
  width: 30px;
  display: flex;
  align-items: center;
  filter: opacity(0.3);
  position: fixed;
  background: white;
  border-color: black;
  transition: all 0.2s;
}

.shuffleIcon {
  transform: scale(1.5);
}

.volSlider {
  z-index: 5;
  top: 40px;
  right: 4px;
  height: 10px;
  width: 173px;
  filter: opacity(0.3);
  position: fixed;
  background: white;
  transition: all 0.2s;
}

/* ----- Social Links ----- */

.vidLink {
  z-index: 5;
  position: absolute;
  top: 3px;
  right: 5px;
  font-family: Arial, monospace;
  -webkit-text-stroke: 0.15px white;
  filter: opacity(0.3);
  transition: all 0.2s;
}

.vidLinkButton {
  border-radius: 50%;
  height: 30px;
  width: 30px;
  display: flex;
  align-items: center;
  background: white;
  border-color: black;
}

.vidLinkIcon {
  transform: scale(1.25);
}

/* ----- Help Popup ----- */

.helpBox {
  z-index: 5;
  top: 3px;
  right: 151px;
  height: 30px;
  width: 30px;
  content: '?';
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: white;
  border-radius: black;
  position: absolute;
  filter: opacity(0.3) drop-shadow(2px 4px 6px black);
  transition: all 0.2s;
}

.popupBox {
  z-index: 5;
  position: fixed;
  background: none;
}

.box {
  z-index: 5;
  position: relative;
  margin: 0;
  height: fit-content;
  width: 50vw;
  max-width: 600px;
  min-width: 350px;
  background: rgb(211, 211, 211, 0.75);
  border-radius: 10px;
  padding: 20px;
  border: 1px solid black;
  font-family: Arial, monospace;
  padding-bottom: 50px;
  filter: drop-shadow(2px 4px 6px black);
}

.closeIcon {
  z-index: 5;
  position: fixed;
  right: calc(1%);
  top: calc(1%);
  background: #ededed;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  line-height: 20px;
  text-align: center;
  border: 1.5px solid black;
  font-size: 20px;
  filter: opacity(0.3);
  transition: all 0.2s;
}

.helpBoxTitle {
  margin-top :0;
}

.dontShowPopup {
  z-index: 5;
  bottom: 5%;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1.5px solid black;
  border-radius: 10%;
  background: rgb(255,255,255,0.75);
  position: absolute;
  color: black;
  filter: opacity(0.3);
  transition: all 0.2s;
}

/*----- Credits -----*/

.credits {
  z-index: 5;
  position: relative;
  height: fit-content;
  font-family: Arial, monospace;
}

.creditsBox {
  z-index: 5;
  top: 80px;
  right: -15px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1.5px solid black;
  border-radius: 10%;
  background: rgb(255,255,255,0.75);
  position: absolute;
  color: black;
  filter: opacity(0.3);
  transition: all 0.2s;
}

.popupCredits {
  z-index: 5;
  position: fixed;
  background: none;
}

.boxCredits {
  z-index: 5;
  position: relative;
  margin: 0;
  height: fit-content;
  width: 50vw;
  max-width: 500px;
  min-width: 350px;
  background: rgb(211, 211, 211, 0.75);
  border-radius: 10px;
  padding: 20px;
  border: 1px solid black;
  font-family: Arial, monospace;
  filter: drop-shadow(2px 4px 6px black);
}

.closeCreditsIcon {
  z-index: 5;
  position: fixed;
  right: calc(1%);
  top: calc(2%);
  background: #ededed;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  line-height: 20px;
  text-align: center;
  border: 1.5px solid black;
  font-size: 20px;
  filter: opacity(0.3);
  transition: all 0.2s;
}

/* ----- General Hover Stylings ----- */

.closeIcon:hover      ,   .helpBox:hover,
.rtRefLink:hover      ,   .vidLink:hover, 
.muteButton:hover     ,   .muteIcon:hover,
.shuffleButton:hover  ,   .shuffleIcon:hover,
.menuButton:hover     ,   .closeMenuIcon:hover,
.creditsBox:hover     ,   .closeCreditsIcon:hover,
.volSlider:hover      ,   .dontShowPopup:hover {
  filter: opacity(1) drop-shadow(2px 4px 6px black);
}

/* ----- Mobile Stylings ----- */

@media screen and (min-width: 340px) {
  .popupBox {
    top: 10%;
  }

  .popupCredits {
    z-index: 5;
    bottom: 3%;
    position: fixed;
    background: none;
  }

  .boxMenu {
    scale: 0.9;
    z-index: 5;
    position: fixed;
    top: 60px;
    right: -2.5%;
    width: 200px;
    margin: 0;
    height: 100px;
    min-height: 10vh;
    background-color: rgba(255,255,255,0.5);
    border-radius: 10px;
    padding: 20px;
    border: 1px solid black;
    font-family: Arial, monospace;
    filter: drop-shadow(2px 4px 6px black);
  }

  .music {
    scale: 0.8;
    z-index: 5;
    height: 125px;
    width: 245px;
    position: absolute;
    top: -1%;
    left: -6%;
    border: none;
    transition: all 0.2s;
  }

  .music iframe {
    height: 125px;
    width: 245px;
    filter: opacity(0.1);
    transition: all 0.2s;
  }

  .music iframe:hover {
    height: 450px;
    width: 450px;
    filter: opacity(1) drop-shadow(2px 4px 6px black);
  }
}